import Tile from 'ol/layer/tile';
import Group from 'ol/layer/group';
import XYZ from 'ol/source/xyz';
import Attribution from 'ol/attribution';
import Proj from 'ol/proj';

import { lakes } from './ksj';

const gsiAttribution = new Attribution({
  html: '<a href="https://maps.gsi.go.jp/development/ichiran.html" target="_blank"></a>'
});

const JapanExtent = Proj.transformExtent([120, 20, 154, 46], 'EPSG:4326', 'EPSG:3857');

export const cjstd = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/std/{z}/{x}/{y}.png',
    attributions: [gsiAttribution],
    maxZoom: 18,
    minZoom: 2,
  }),
  title: '',
  subtitle: 'GSI Maps',
  summary: ''
});

export const cjpale = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/pale/{z}/{x}/{y}.png',
    attributions: [gsiAttribution],
    maxZoom: 18,
    minZoom: 2,
  }),
  title: '()',
  subtitle: 'GSI Maps (pale)',
  summary: ''
});

export const seamless = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/seamlessphoto/{z}/{x}/{y}.jpg',
    attributions: [gsiAttribution],
    maxZoom: 18,
    minZoom: 2,
  }),
  title: '',
  subtitle: 'GSI Seemless Photo Maps',
  summary: ''
});

export const photo70s = new Group({
  layers: [
    new Tile({
      source: new XYZ({
        url: '//cyberjapandata.gsi.go.jp/xyz/blank/{z}/{x}/{y}.png',
        attributions: [gsiAttribution],
        maxZoom: 14,
        minZoom: 6,
      }),
      extent: JapanExtent,
      minResolution: 152.88
    }),
    new Tile({
      source: new XYZ({
        url: '//cyberjapandata.gsi.go.jp/xyz/gazo1/{z}/{x}/{y}.jpg',
        attributions: [gsiAttribution],
        maxZoom: 17,
        minZoom: 10,
      }),
      extent: JapanExtent,
      maxResolution: 152.88,
    })
  ],
  title: "(70's)",
  subtitle: "GSI 70's Photo Maps",
  summary: '19741978'
});

export const photo70sPlain = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/gazo1/{z}/{x}/{y}.jpg',
    attributions: [gsiAttribution],
    maxZoom: 17,
    minZoom: 10,
  }),
  title: "(70's)",
  extent: JapanExtent,
  maxResolution: 152.88,
  subtitle: "GSI 70's Photo Maps",
  summary: '19741978'
});

export const blank = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/blank/{z}/{x}/{y}.png',
    attributions: [gsiAttribution],
    maxZoom: 14,
    minZoom: 6,
  }),
  title: "",
  extent: JapanExtent,
  subtitle: "GSI Blank Map",
  summary: ''
});

export const relief = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/relief/{z}/{x}/{y}.png',
    attributions: [gsiAttribution],
    maxZoom: 15,
    minZoom: 5,
  }),
  extent: JapanExtent,
  title: '',
  subtitle: 'GSI Relief Map',
  summary: ''
});

export const hillshade = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/hillshademap/{z}/{x}/{y}.png',
    attributions: [gsiAttribution],
    maxZoom: 16,
    minZoom: 2,
  }),
  extent: JapanExtent,
  title: '',
  subtitle: 'GSI Hillshade Map',
  summary: ''
});

export const slope = new Tile({
  source: new XYZ({
    url: '//cyberjapandata.gsi.go.jp/xyz/slopemap/{z}/{x}/{y}.png',
    attributions: [gsiAttribution],
    maxZoom: 15,
    minZoom: 3,
  }),
  extent: JapanExtent,
  title: '',
  subtitle: 'GSI Slope Map',
  summary: ''
});

export const specialRelief = new Group({
  layers: [
    new Tile({
      source: new XYZ({
        url: '//cyberjapandata.gsi.go.jp/xyz/pale/{z}/{x}/{y}.png',
        maxZoom: 18,
        minZoom: 2,
      }),
      maxResolution: 4.77
    }),
    new Tile({
      source: new XYZ({
        url: '//cyberjapandata.gsi.go.jp/xyz/relief/{z}/{x}/{y}.png',
        maxZoom: 15,
        minZoom: 5,
      }),
      extent: JapanExtent,
      opacity: 0.7
    }),
    lakes,
    new Tile({
      source: new XYZ({
        url: '//cyberjapandata.gsi.go.jp/xyz/slopemap/{z}/{x}/{y}.png',
        attributions: [gsiAttribution],
        maxZoom: 15,
        minZoom: 3,
      }),
      extent: JapanExtent,
    }),
  ],
  title: '',
  subtitle: 'Special Terrain Maps',
  summary: ''
});



